@import "../../global.scss";

.intro{
    background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }
    .left{
        flex: 0.5;
        overflow: hidden;
        .imgContainer{
            width: 818px;
            height: 827px;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;
            @include mobile{
                align-items:flex-start;
            }

            img{
                height: 90%;
            }
        }
    }
    .right{
        flex: 0.5;
        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;

            h1{
                font-size: 45px;
                margin: 10px 0;
            }
            h2{
                font-size: 28px;
            }
            h3{
                font-size: 30px;
                
            }
            h4{
                font-size: 18px;
                margin: 10px 0;
                span{
                    font-size: inherit;
                    color: rgb(133, 168, 221);;
                }

                .ityped-cursor{
                    animation: blink 1s infinite;
                }
                @keyframes blink{
                    
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }

            }

        }
        a{
            position: absolute;
            bottom: 10px;
            
            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }

            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}